.io-projects-container {
    margin-top: 50px;
    -ms-flex-direction: column;
        flex-direction: column;
    max-width: 100%;
}

.io-project-content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.io-project-card {
    padding: 10px 20px 10px 10px;
    margin-bottom: 10px;
    border: 1px solid #000000;
    -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
            box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    border-radius: 5px;
    width: 800px;
    display: -ms-flexbox;
    display: flex;
}

.io-project-picture {
    height: 300px;
    width: 370px;
}

.io-project-header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-line-pack: center;
        align-content: center;
}

.io-project-header > a > svg {
    margin-top: 20px;
}

.io-project-tech-icon {
    height: 70px;
    width: 73px;
    margin-right: 5px;
}

.io-tooltip {
    position: relative;
    display: inline-block;
}

.io-tooltip .io-tooltip-text {
    font-size: 16px;
    visibility: hidden;
    width: 120px;
    color: #ffffff;
    text-align: center;
    padding-top: 5px;
    position: absolute;
    z-index: 1;
    top: 90%;
    left: 50%;
    margin-left: -60px;
    opacity: 0;
    -webkit-transition: opacity 1s;
    -o-transition: opacity 1s;
    transition: opacity 1s;
}

.io-tooltip:hover .io-tooltip-text {
    visibility: visible;
    opacity: 1;
}


